export default Form04Page;

const SSection = styled.section`
  width: calc(100% - 4rem);
  height: calc(100% - 2rem);
  margin: auto;
  padding: 2rem;
  background-color: ${Colors.light};
  border-radius: 8px;
`;

const SSlide = styled.div`
  width: 100%;
  height: 100%;
`;

const SForm = styled.div`
  min-height: calc(100% - 64px - 64px - 3rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  h3 {
    margin-top: 4rem;
    color: ${Colors.dark};
    opacity: 0.7;
  }
  div {
    width: 220px;
    label {
      &:first-of-type {
        margin-top: 3rem;
      }
    }
  }
`;

const SLabel = styled.label`
  display: inline-flex;
  align-items: center;
  margin-top: 1.5rem;
  border-radius: 8px;
  padding: 0;
  font-size: 0.9rem;
  font-weight: 500;
  line-height: 1;
  transition: border 0.2s;
  cursor: pointer;
  user-select: none;
  button {
    width: 1.5rem;
    height: 1.5rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: ${Colors.gray};
    margin-right: 0.5rem;
    border-radius: 8px;
    transition: all 0.2s;
    border: none;
    border: 1px solid ${Colors.gray};
    &:focus {
      outline: none;
      border-color: ${Colors.primary};
    }
    i {
      transition: all 0.2s;
      color: ${Colors.gray};
      transform: scaleX(0);
    }
  }
  input[type="checkbox"] {
    /* display: none; */
    opacity: 0;
    transform: scale(1.7);
    display: inline-flex;
    z-index: 1;
    margin-right: -1.2rem;
    &:checked {
      + button {
        background-color: ${Colors.primary};
        border-color: ${Colors.primary};
        i {
          color: ${Colors.light};
          transform: scaleX(1);
        }
      }
    }
  }
  &:focus {
    outline: none;
    border: 1px solid ${Colors.primary};
  }
`;
